import { createRouter, createWebHistory } from 'vue-router'
import Layout from '../layout/Layout'

const routes = [
    {
        path: '/',
        name: 'Layout',
        component: Layout,
        redirect: "/login",
        children: [
            {
                path: 'user',
                name: 'User',
                component: () => import("@/views/User"),
                meta: {
                    requireAuth: true
                }

            },
            {
                path: 'chip',
                name: 'Chip',
                component: () => import("@/views/Chip"),
                meta: {
                    requireAuth: true
                }

            },
            {
                path: 'manufacturer',
                name: 'Manufacturer',
                component: () => import("@/views/Manufacturer"),
                meta: {
                    requireAuth: true
                }

            },
            {
                path: 'project',
                name: 'Project',
                component: () => import("@/views/Project"),
                meta: {
                    requireAuth: true
                }

            },
            {
                path: 'product',
                name: 'Product',
                component: () => import("@/views/Product"),
                meta: {
                    requireAuth: true
                }

            },
            {
                path: 'map1',
                name: 'Map1',
                component: () => import("@/views/Map1"),
                meta: {
                    requireAuth: true
                }

            }

        ]
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import("@/views/Login")
    },
    // {
    //     path: '/register',
    //     name: 'Register',
    //     component: () => import("@/views/Register")
    // },
    // {
    //     path: '/person',
    //     name: 'Person',
    //     component: () => import("@/views/Person")
    // },
    // {
    //     path: '/password',
    //     name: 'Password',
    //     component: () => import("@/views/Password")
    // }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router